<template>
  <div>
    <p>영화</p>
    <ul>
      <li v-for="movieItem in movies" v-bind:key="movieItem.id">
        {{ movieItem.name }}
        <router-link :to="{ name: 'Sub', params: { id: movieItem.id } }">버튼</router-link>
      </li>
    </ul>
  </div>
</template>

<script>
export default {
  created() {
    this.$http.get("/api/main").then(response => {
      console.log(response.data);
      this.movies = response.data;
    });
  },
  data() {
    return {
      movies: [],
    };
  },
};
</script>

